<template>
  <profile-layout>
    <go-top />
    <top-section-layout v-if="userExists">
      <b-overlay :show="loading || autoToggleRxUser == undefined">
        <card
          shadow
          class="card-profile mt--300"
          no-body
        >
          <div class="px-4">
            <div class="row justify-content-center">
              <div class="col-lg-3 order-lg-2 hover-big-shadow">
                <div class="card-profile-image">
                  <a href="#">
                    <img
                      v-show="!loading"
                      v-lazy="`https://a.ppy.sb/${autoToggleRxUser.id}`"
                      class="rounded-circle"
                      width="400px"
                    >
                  </a>
                </div>
              </div>
              <div class="col-lg-4 order-lg-3 text-lg-right align-self-lg-center">
                <div 
                  
                  class="card-profile-actions py-4 mt-lg-0 "
                >
                  <base-button
                    v-show="SBLoggedIn()"
                    type="info"
                    size="sm"
                    class="mr-4"
                  >
                    {{ $t("user.addAsFriend") }}
                  </base-button>
                  <base-button
                    v-show="SBLoggedIn()"
                    type="default"
                    size="sm"
                    class="float-right"
                  >
                    {{ $t("user.block") }}
                  </base-button>
                </div>
                <div>
                  <span class="align-top mr-2">{{ $t("user.toggleRx") }}:</span>
                  <base-switch
                    id="input-sm"
                    v-model="rxStat"
                    size="sm"
                  />
                </div>
              </div>
              <div class="col-lg-4 order-lg-1">
                <div
                  v-if="autoToggleRxUser[strMode]"
                  class="card-profile-stats d-flex justify-content-center"
                >
                  <div
                    v-for="(v,k) in {
                      pp: numbers.pp, 
                      accuracy: smallerFloatHtml(`${numbers.accuracy.toFixed(2)}%`),
                      playCount: numbers.playcount,
                      //TTH: autoToggleRxUser[strMode].total_hits,
                    }"
                    :key="`user-stat-${k}`"
                  >
                    <span
                      class="heading"
                      v-html="v"
                    />
                    <span
                      class="description"
                      v-html="$t(`user.${k}`)"
                    />
                  </div>
                </div>
              </div>
            </div>
            <div
              v-if="autoToggleRxUser[strMode]"
              class="text-center mt-5"
            >
              <h3>
                {{ autoToggleRxUser.username }}
                <small
                  v-if="autoToggleRxUser.username_aka"
                  class="font-weight-light"
                >( {{ $t("user.aka", {aka: autoToggleRxUser.username_aka}) }} )</small>
              </h3>
              <div class="h6 font-weight-300">
                <!-- <i class="ni location_pin mr-2" /> -->
                {{ $t("user.globalRank",{rank: numbers.globalRank || 0}) }}
              </div>
              <div class="h6 font-weight-300">
                <gb-flag
                  :code="autoToggleRxUser.country"
                  size="small"
                  icon-path="/assets/flags"
                />
                #{{ numbers.countryRank || 0 }}
              </div>
              <div class="h6 mt-4">
                <i class="ni business_briefcase-24 mr-2" />
                <!-- Last seen at {{ moment(autoToggleRxUser.latest_activity).fromNow() }} -->
                {{ $t("user.lastSeenAt",{fromNow: lastSeenAt}) }}
              </div>
              <div>
                <i class="ni education_hat mr-2" />
                <!-- registered at {{ moment(autoToggleRxUser.registered_on).format('LLL') }} -->
                {{ $t("user.registeredAt", {at:registeredAt}) }}
              </div>
            </div>
            <div class="mt-5 py-5 border-top text-left">
              <p v-html="userpage" />
              <!-- <div class="row justify-content-center text-center">
                                <div class="col-lg-9">
                                    <a href="#">Show more</a>
                                </div>
                            </div>-->
              <mode-switcher v-model="numericMode" />
            </div>
          </div>
        </card>
      </b-overlay>
      <!-- <p>{{ $t("message") }}</p> -->
    </top-section-layout>
    <section-layout
      v-if="userExists && !loading"
      bg-variant="success"
      skew
      top-overflow-hidden
      shaped
    >
      <bp-card
        :id="id"
        :rx="rxStat"
        :mode="numericMode"
      />
    </section-layout>
    <section-layout
      section-class="py-0"
      container-class="py-0"
    >
      <card shadow>
        <mode-switcher v-model="numericMode" />
      </card>
    </section-layout>
    <section-layout
      v-if="userExists && !loading"
      :contained="undefined"
      bg-variant="warning"
      skew
      shaped
      last
    >
      <recent-card
        :id="id"
        :rx="rxStat"
        :mode="numericMode"
      />
    </section-layout>
  </profile-layout>
</template>44
<script>
const axios = require("axios");
import {gsap,Power1} from "gsap"
import bbCodeParser from "js-bbcode-parser";
const defaultCodes = [...bbCodeParser.codes];

import GoTop from "@inotom/vue-go-top";
import BpCard from "@/components/sb-components/BPCard";
import RecentCard from "@/components/sb-components/RecentCard";
import ModeSwitcher from "@/components/sb-components/ModeSwitcher";
import SectionLayout from "@/components/sb-layouts/components/SectionLayout";
import TopSectionLayout from "@/components/sb-layouts/components/TopSectionLayout";
import ProfileLayout from "@/components/sb-layouts/ProfileLayout";
const defaultUser = {
  id: undefined,
  name: "peppy"
};
export default {
  components: {
    BpCard,
    RecentCard,
    ModeSwitcher,
    GoTop,
    SectionLayout,
    ProfileLayout,
    TopSectionLayout
  },
  props: {
    handle: {
      type: [String, Number],
      default: -1
    },
    mode: {
      type: String,
      default: "std"
    },
    rx: {
      type: String,
      default: ""
    }
  },
  data: () => ({
    numbers:{
      pp: 0,
      accuracy: 0,
      playcount: 0,
      globalRank: 0,
      countryRank: 0
    },
    user: {},
    userRx: {},
    userpageRaw: "",
    userpage: "",
    id: undefined,
    selectedMode: 0,
    logger: true,
    loading: true,
    userExists: true
  }),
  computed: {
    numericMode: {
      get() {
        switch (this.mode) {
          case "std":
          case "osu":
            return 0;
          case "taiko":
            return 1;
          case "catch":
          case "ctb":
            return 2;
          case "mania":
            return 3;
          default:
            return 0;
        }
      },
      set(newVal) {
        this.$router.push({
          name: "users",
          params: {
            handle: this.id,
            mode: this.toStrMode(newVal),
            rx: this.rx ? this.rx : undefined
          }
        });
      }
    },
    registeredAt() {
      return this.moment(this.autoToggleRxUser.registered_on).format("LLL");
    },
    lastSeenAt() {
      return this.moment(this.autoToggleRxUser.latest_activity).fromNow();
    },
    autoToggleRxUser: {
      get() {
        return this.rxStat ? this.userRx : this.user;
      },
      set(user) {
        if (this.rxStat) {
          this.userRx = user;
        } else {
          this.user = user;
        }
      }
    },
    pp(){
      return this.autoToggleRxUser[this.strMode].pp
    },
    accuracy(){
      return this.autoToggleRxUser[this.strMode].accuracy
    },
    playcount(){
      return this.autoToggleRxUser[this.strMode].playcount
    },
    globalRank(){
      return this.autoToggleRxUser[this.strMode].global_leaderboard_rank || 0
    },
    countryRank(){
      return this.autoToggleRxUser[this.strMode].country_leaderboard_rank || 0
    },
    strMode() {
      return this.toStrMode();
    },
    rxStat: {
      get() {
        return Boolean(this.rx == "rx");
      },
      set(newVal) {
        if (typeof newVal == "object") return;
        this.$router.push({
          name: "users",
          params: {
            handle: this.id,
            mode: this.mode,
            rx: newVal ? "rx" : undefined
          }
        });
      }
    }
  },
  watch: {
    async handle() {
      this.user = {};
      this.userRx = {}
      await this.initUserId();
      await this.switchUser();
      this.$emit("updateHead");
    },
    mode() {
      this.$emit("updateHead");
    },
    rx() {
      // await this.initUserId();
      this.switchUser();
      this.$emit("updateHead");
    },
    autoToggleRxUser(){
      gsap.to(this.$data.numbers, {
        duration: 0.5, 
        pp: this.pp,
        accuracy: this.accuracy,
        playcount: this.playcount,
        globalRank: this.globalRank,
        countryRank: this.countryRank,
        ease: Power1.easeOut,
        roundProps:"pp,playcount,globalRank,countryRank"
      });
    }
  },
  beforeCreate() {
    const l2Parser = {};
    l2Parser.__proto__ = bbCodeParser.__proto__;
    l2Parser.setCodes({
      "\\[center\\](.+?)\\[/center\\]": '<div class="text-center">$1</div>'
    });
    const newCodes = l2Parser.codes;
    bbCodeParser.codes = [...defaultCodes, ...newCodes];
  },
  created() {
    this.init();
    // this.storeShit("sbStore", {});
    // const ref = this.getShit("sbStore");
    // ref.sb = this.$props;
    // this.storeShit("sbStore", ref);
    // this.log(this.getShit("sb"));
  },

  methods: {
    async init() {
      this.log(
        {
          handle: this.handle,
          mode: this.mode,
          rx: this.rxStat,
          numericMode: this.numericMode
        },
        "router-params"
      );
      // this.loading = true
      await this.initUserId();
      await this.initUser();
      await this.initUserpage();
      this.loading = false;
    },
    async initUserId() {
      const apiBase = this.$root.rippleApiBase;

      if (!isNaN(this.handle)) this.id = parseInt(this.handle);
      else {
        try {
          const result = await axios({
            url: `${apiBase}/users/whatid`,
            params: {
              name: this.handle
            }
          }).then(res => res.data);
          this.id = result.id;
          this.userExists = true;
        } catch (error) {
          this.log(error);
          this.id = undefined;
          this.userExists = false;
          // this.loading = false;
        }
      }
    },
    async initUser(rxStat = this.rxStat) {
      const apiBase = this.$root.rippleApiBase;
      await axios({
        url: `${apiBase}/users/${rxStat ? "rx" : ""}full`,
        params: {
          id: this.id
        }
      })
        .then(res => res.data)
        .then(user => {
          // user.rx = rxStat;
          // this.autoToggleRxUser = user;
          if (rxStat) this.userRx = user;
          else this.user = user
          this.userExists = true;
        })
        .then(_ => this.$emit("updateHead"))
        .catch(error => {
          this.log(error);
          this.userExists = false;
          return {};
        });
      this.initUser(!rxStat)
    },
    async initUserpage() {
      const apiBase = this.$root.rippleApiBase;
      this.userpageRaw = "";
      this.userpage = "";

      const userpage = await axios({
        url: `${apiBase}/users/userpage`,
        params: {
          id: this.id
        }
      }).then(res => res.data);

      this.userpageRaw = userpage.userpage;
      this.userpage = this.convertBBCode(this.userpageRaw);
    },
    async switchUser() {

      this.log(
        {
          required: {
            rx: this.rxStat,
            id: this.id
          },
          current:
            Object.entries(this.autoToggleRxUser).length > 0
              ? this.autoToggleRxUser
              : "lazy-load"
        },
        "select-user",
        {
          deep: 2
        }
      );
      if (this.id !== this.autoToggleRxUser.id) {
        this.loading = true
        // this.clearUser()
        await this.initUser();
        await this.initUserpage();
        this.loading = false
      }
    },
    // clearUser(){
    //   this.user = {}
    //   this.userRx = {}
    // },
    convertBBCode(bbcode) {
      bbcode = bbcode.split("\n").join("<br>");
      bbcode = this.replaceEmoji(bbcode);
      bbcode = bbCodeParser.parse(bbcode);
      return bbcode;
    },
    replaceEmoji(html) {
      Object.entries({
        ":peppy:": '<img src="/static/emotes/peppy.png">',
        ":barney:": '<img src="/static/emotes/barney.png">',
        ":akerino:": '<img src="/static/emotes/akerino.png">',
        ":foka:": '<img src="/static/emotes/foka.png">',
        ":kappy:": '<img src="/static/emotes/kappy.png">',
        ":creepypeppy:": '<img src="/static/emotes/creepypeppy.png">',
        ":peppyfiero:": '<img src="/static/emotes/peppyfiero.png">',
        ":djpeppy:": '<img src="/static/emotes/djpeppy.png">',
        ":kappa:": '<img src="/static/emotes/kappa.png">'
      }).map(([occur, replace]) => (html = html.split(occur).join(replace)));
      return html;
    },
    toStrMode(numericMode = this.numericMode) {
      switch (numericMode) {
        case 0:
          return "std";
        case 1:
          return "taiko";
        case 2:
          return "ctb";
        case 3:
          return "mania";
        default:
          break;
      }
    },
    smallerFloatHtml(str) {
      const splitted = str.split(".");
      return `${splitted[0]}<small>.${splitted[1]}</small></span>`;
    }
  },
  head: {
    title: function() {
      const separator = " | ";
      const arr = [this.autoToggleRxUser.username || "loading"];
      arr.push(this.strMode, this.rx ? "relax" : "vainilla");
      const inner = arr.join(separator);
      return {
        inner
      };
    }
  }
};
</script>
<style>
.hover-big-shadow::before {
  content: "";
  height: 100%;
  width: 100%;
  /* display: inline-block; */
  position: absolute;
  background-image: linear-gradient(
    -90deg,
    #d077f3 0%,
    #009bff 47%,
    #2ac9db 100%
  );
  opacity: 0;
  border-radius: 100px;
  filter: blur(5em);
  z-index: -1;
  transition: all 0.15s ease;
}
.hover-big-shadow:hover::before {
  content: "";
  opacity: 0.3;
  transition: all 0.15s ease;
}
</style>